<template><section><html><head></head><body><h1>Model</h1>
<p><code v-pre="">vc-primitive-model</code> 组件用于加载基于 glTF 的三维模型。</p>
<h2>示例</h2>
<h3>加载 glTF 模型</h3>
<h4>预览</h4>
<doc-preview>
