<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 指定 gltf 文件的 url 地址。</td>
</tr>
<tr>
<td>basePath</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 glTF JSON 中的路径相对于的基本路径。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 model 图元是否显示。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 4x4 转换矩阵，用于将模型从模型坐标转换为世界坐标。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 model 缩放比例。</td>
</tr>
<tr>
<td>minimumPixelSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 指定 model 的最小像素。</td>
</tr>
<tr>
<td>maximumScale</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 最大像素。</td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> 指定与 model 关联的信息。</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定与 model 是否可以被拾取。</td>
</tr>
<tr>
<td>incrementallyLoadTextures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定在加载模型后纹理是否可以继续流入。</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 确定在加载所有 glTF 文件后，是否将模型 WebGL 资源创建分散在几个帧或块上，直到完成。</td>
</tr>
<tr>
<td>clampAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定动画在没有帧动画的时候保持最后一个姿势。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> 指定 model 是否投射或接收每个光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4</strong></td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 可选的仅用于调试。 为模型中的每个DrawCommand绘制边界球。</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 可选的仅用于调试。 仅用于调试。 在线框中绘制模型。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 model 的高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>scene</td>
<td>Object</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 对于使用height reference属性的模型必须传递。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 随相机改变的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> 指定 model 渲染混合的颜色。</td>
</tr>
<tr>
<td>colorBlendMode</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 model 与颜色混合模式。 <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong></td>
</tr>
<tr>
<td>colorBlendAmount</td>
<td>Number</td>
<td><code v-pre="">0.5</code></td>
<td><code v-pre="">optional</code> 指定 colorBlendMode 为 MIX 的颜色强度。0 表示模型颜色，1 表示纯色，0-1 表示混合。</td>
</tr>
<tr>
<td>silhouetteColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'RED'</code></td>
<td><code v-pre="">optional</code> 指定 model 轮廓线颜色。</td>
</tr>
<tr>
<td>silhouetteSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 指定 model 轮廓线像素尺寸。</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 屏幕裁剪参数。</td>
</tr>
<tr>
<td>dequantizeInShader</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 确定是否在GPU上对Draco编码的模型进行了反量化。 这减少了编码模型的总内存使用量。</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> 缩放来自地球，天空，大气层和星空盒的基于漫反射和镜面反射图像的照明。</td>
</tr>
<tr>
<td>lightColor</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> 为模型着色时的浅色。 未定义时，将使用场景的灯光颜色。</td>
</tr>
<tr>
<td>luminanceAtZenith</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> 太阳在天顶的亮度，以每平方米千坎德拉为单位，用于该模型的过程环境图。</td>
</tr>
<tr>
<td>sphericalHarmonicCoefficients</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> 用于基于图像的照明的漫反射颜色的三阶球面谐波系数。</td>
</tr>
<tr>
<td>specularEnvironmentMaps</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> KTX文件的URL，其中包含镜面照明的立方体贴图和卷积的镜面mipmap。</td>
</tr>
<tr>
<td>credit</td>
<td>Object|String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 的描述信息。</td>
</tr>
<tr>
<td>backFaceCulling</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 是否剔除背面几何。 如果为true，则背面剔除取决于材质的doubleSided属性； 如果为假，则禁用背面剔除。 如果Model＃color是半透明的或Model＃silhouetteSize大于0.0，则不会剔除背面</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Model.html">Model</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>model</td>
<td>模型可用时触发。 返回模型对象。</td>
</tr>
<tr>
