</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
