<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该图元时触发。</td>
</tr>
